.button {
    max-width: 95px;
    width: 95px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    border: none;
    outline: none;
    border-radius: 10px;

    text-align: center;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    color: #fff;

    cursor: pointer;
    transition-duration: 0.5s;

    &:hover {
        opacity: 0.8;
        transition-duration: 0.5s;
    }

    &:focus {
        transition-duration: 0.5s;
        box-shadow: 0px 0px 6px 2px rgba(0, 0, 0, 0.27);
    }

    &.GREEN {
        background: #2ee5ac;
    }

    &.DARK {
        font-weight: 500;
        background: #7d7d7d;
    }
}
